"use client";

import { useState } from "react";
import {
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Typography,
  Box,
  Divider,
} from "@mui/material";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";

const petrolStations = Array.from({ length: 10 }).map((_, i) => ({
  name: `Petrol Station ${i + 1}`,
  topWarehouses: [
    { warehouse: "Warehouse 3", km: 5, duration: "10 mins" },
    { warehouse: "Warehouse 12", km: 12, duration: "25 mins" },
    { warehouse: "Warehouse 7", km: 15, duration: "35 mins" },
  ],
}));

const Mapping = () => {
  const [expanded, setExpanded] = useState(false);

  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  return (
    <Box
      sx={{
        minHeight: "500px", // üî• minimum height
        maxHeight: "80vh", // optional: prevents taking up full screen
        overflowY: "auto", // üî• scroll enabled
        p: 2,
        borderRadius: 2,
        border: "1px solid #e0e0e0",
      }}
    >
      {petrolStations.map((station, index) => (
        <Accordion
          key={index}
          expanded={expanded === index}
          onChange={handleChange(index)}
          sx={{
            mb: 1,
            borderRadius: 2,
            "&:before": { display: "none" },
            boxShadow: "0 1px 4px rgba(0,0,0,0.1)",
          }}
        >
          <AccordionSummary expandIcon={<ExpandMoreIcon />}>
            <Typography fontWeight="bold">{station.name}</Typography>
          </AccordionSummary>

          <AccordionDetails>
            {station.topWarehouses.map((wh, i) => (
              <Box key={i} sx={{ mb: 1 }}>
                <Typography>
                  <b>{wh.warehouse}</b> ‚Äî {wh.km} km, {wh.duration}
                </Typography>
                {i !== 2 && <Divider sx={{ my: 1 }} />}
              </Box>
            ))}
          </AccordionDetails>
        </Accordion>
      ))}
    </Box>
  );
};

export default Mapping;
